import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Modal from '@material-ui/core/Modal';
import { signOut, getCookie } from '../../../helpers/auth';
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import ArrowBackIosOutlinedIcon from '@material-ui/icons/ArrowBackIosOutlined';
import BlockIcon from '@material-ui/icons/Block';
import ReportIcon from '@material-ui/icons/Report';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

export default function SimpleMenu({ id, onClick }) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleClickBlock = () => {
        setOpen(true)
    }
    // handleAB = handle Agree block
    const handleAB = () => {
        const idSend = getCookie().token
        axios.post("http://localhost:2704/api/msgC/blockUser", { idSend, id })
            .then(res => {
                toast.success("Yu cu ca bn  c thc hin")
                setOpen(false)
                setAnchorEl(null)
            }).catch(err => {
                console.log(err)
            })
    }
    const handleCloseBlock = () => {
        setOpen(false)
    }
    const handleLogOut = () => {
        signOut();
        window.location.reload(false);
    }
    return (
        <div className="menuMessage" >
            <ToastContainer />
            <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
                &#9776;
            </Button>
            <Menu
                id="menuContainerMessage"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={handleClose}>
                    <span
                        className="simple_menu_span"
                        onClick={() => onClick("")}
                        to="/"
                    >
                        <ArrowBackIosOutlinedIcon /> Quay v
                    </span>
                </MenuItem>
                <MenuItem>
                    <span
                        className="simple_menu_span"
                        onClick={handleClickBlock}>
                        <BlockIcon />    Chn
                    </span>
                    <Modal
                        open={open}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description" >
                        <div className="contain_block">
                            <div className="head">
                                <button onClick={handleCloseBlock}>&#10005;</button>
                            </div>
                            <div className="middle">
                                <p>Bn c chc mun chn ngi ny, nu ngi ny gy hi cho bn bn c th bo co chng ti s gip bn</p>
                            </div>
                            <div className="bottom">
                                <button onClick={handleAB}>Xc nhn</button>
                                <button
                                    onClick={handleCloseBlock}
                                    className="cancel">Hy b</button>
                            </div>
                        </div>
                    </Modal>
                </MenuItem>
                <MenuItem >
                    <span
                        className="simple_menu_span"
                    >
                        <ReportIcon />   Bo co
                    </span>
                </MenuItem>
                <MenuItem onClick={handleLogOut}>
                    <span className="simple_menu_span">
                        <ExitToAppIcon /> ng xut
                    </span>
                </MenuItem>
                {/* <MenuItem onClick={}>Logout</MenuItem> */}
            </Menu>
        </div>
    );
}